import PortfolioList from "../portfolioList/PortfolioList";
import "./portfolio.scss";
import {useEffect, useState} from "react";
import {engineering, graphicdesign, development} from "../../data";

export default function Portfolio() {
    const [selected, setSelected] = useState("featured");
    const [data, setData] = useState([]);


    const list = [
        {
          id: "engineering",
          title: "Engineering",
        },
        {
          id: "graphicdesign",
          title: "Graphic Design",
        },
        {
          id: "development",
          title: "Development",
        },
      ];
    
      useEffect(()=>{

        switch(selected){
            case "engineering":
                setData(engineering);
                break;

            case "graphicdesign":
                setData(graphicdesign);
                break;
                
            case "development":
                setData(development);
                break;
            default:
                setData(engineering);
        }

      },[selected])

    return (
      <div className="page">
        <div className="portfolio" id = "portfolio">
            <h1>Tools I Use</h1>
            <ul>
                {list.map((item)=>(
                    <PortfolioList 
                    title = {item.title} 
                    active={selected === item.id} 
                    setSelected={setSelected}
                    id = {item.id}
                    />
                ))}
            </ul>

            <div className="container">
                {data.map( d=> (

                <div className="item">
                    <img src={d.img} alt="" />
                    <h3>{d.title}</h3>
                </div>

                ))}
                
            </div>
        </div>
        </div>
    )
}
